body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.grid {
  display: grid;
  grid-gap: 1em;
  grid-template-areas:
    "header"
    "left"
    "middle"
    "right"
    "footer";
    max-width: 980px;
    margin: 0 auto;
    padding: 0 25px;
}

.left {
  grid-area: left;
}

.right {
  grid-area: right;
}

.middle {
  grid-area: middle;
}

.header {
  grid-area: header;
}

.footer {
  grid-area: footer;
}

.joke-btn {
  width: 200px;
  height: 50px;
  padding: 10px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.3rem;
  border: 1px solid #222222;
  -webkit-transition: 300ms ease all;
  transition: 300ms ease all;
}

.joke-btn:hover {
  background-color: #ffa500;
  -webkit-transition: 300ms ease all;
  transition: 300ms ease all;
}

.joke-btn::after {
  content: "again!";
}

@media only screen and (min-width: 500px) {

  .grid {
    grid-template-columns: 20% auto;
    grid-template-areas:
      "header header"
      "left middle"
      "right right"
      "footer footer";
  }
}

@media only screen and (min-width: 600px) {

  .grid {
    grid-gap: 20px;
    grid-template-columns: 25% auto 25%;
    grid-template-areas:
      "header header header"
      "left middle right"
      "footer footer footer";
  }
}
.header {
  background-color: #222222;
  width: 100%;
  height: 25px;
}
.riddle-answer-box {
  height: 25px;
  width: 100%;
  padding: 10px;
  display: block;
  margin-bottom: 20px;
  border: 1px solid #222222;
  text-align: center;
  line-height: 23px;
}

.riddle-answer-box::after {
  content: "Hover for answer!";
}

.riddle-answer-box:hover + .riddle-answer {
  display: block;
  -webkit-transition: 300ms ease all;
  transition: 300ms ease all;
}

.riddle-answer {
  display: none;
  -webkit-transition: 300ms ease all;
  transition: 300ms ease all;
}
.footer {
  background-color: #222222;
  width: 100%;
  height: 25px;
}
